*{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-color: #bda8a8;
  color: aliceblue;
}

body{
  padding: 60px;
}


input{
  font-size: 1.3rem;
  width: 100%;
  color: gray;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;

}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}